  outline: none;
  border-radius: 0px;
  color: #2c2a59;
  font-size: 14px;
}

.indi-form-text label {
  position: absolute;
  bottom: 0;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  border-bottom: 1px solid #f2f2f8;
}

.indi-form-text label::after {
  content: "";
  position: absolute;
  height: 100%;
  left: 0px;
  bottom: -1px;
  width: 100%;
  border-bottom: 1px solid #2c2a59;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.indi-form-text label .content-name {
  position: absolute;
  bottom: 5px;
  left: 0px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  font-family: "NunitoSemibold";
  font-size: 14px;
  color: #707070;
}

.indi-form-text input:focus + .label-name .content-name,
.indi-form-text input:valid + .label-name .content-name {
  -webkit-transform: translateY(-125%);
          transform: translateY(-125%);
  font-size: 14px;
  color: grey;
}

.indi-form-text input:focus + .label-name::after,
.indi-form-text input:valid + .label-name::after {
