.navigation .nav-pannel .nav-list {
  margin-left: 25px;
  position: relative;
  text-align: left;
}

.navigation .nav-pannel .tabs li {
  /* Makes a horizontal row */
  float: left;
  width: 100%;
  /* So the psueudo elements can be
               abs. positioned inside */
  position: relative;
}

.navigation .nav-pannel .tabs a {
  /* Make them block level
               and only as wide as they need */
  float: left;
  padding: 15px 50px;
  text-decoration: none;
  width: 100%;
  font-family: "NunitoRegular";
  font-size: 14px;
  /* Default colors */
  color: white;
  background: #7169ff;
  /* Only round the top corners */
  -webkit-border-top-left-radius: 100px;
  -webkit-border-bottom-left-radius: 100px;
  border-top-left-radius: 100px;
  border-bottom-left-radius: 100px;
  cursor: pointer;
}

.navigation .nav-pannel .tabs a svg {
  margin-right: 15px;
}

.navigation .nav-pannel .tabs .active {
  /* Highest, active tab is on top */
  z-index: 3;
}

.navigation .nav-pannel .tabs .active a {
  /* Colors when tab is active */
  background: #f9f9fb;
  color: #7169ff;
}

.navigation .nav-pannel .tabs .active a svg g {
  stroke: #7169ff;
}

.navigation .nav-pannel .tabs li:before,
.navigation .nav-pannel .tabs li:after,
.navigation .nav-pannel .tabs li a:before,
.navigation .nav-pannel .tabs li a:after {
  /* All pseudo elements are 
               abs. positioned and on bottom */
  position: absolute;
  right: 0;
}

.navigation .nav-pannel .tabs li:last-child:after,
.navigation .nav-pannel .tabs li:last-child a:after,
.navigation .nav-pannel .tabs li:first-child:before,
.navigation .nav-pannel .tabs li:first-child a:before,
.navigation .nav-pannel .tabs .active:after,
.navigation .nav-pannel .tabs .active:before,
.navigation .nav-pannel .tabs .active a:after,
.navigation .nav-pannel .tabs .active a:before {
  content: "";
}

.navigation .nav-pannel .tabs .active:before,
.navigation .nav-pannel .tabs .active:after {
  background: #f9f9fb;
  /* Squares below circles */
  z-index: 1;
}

.navigation .nav-pannel .tabs li:before,
.navigation .nav-pannel .tabs li:after {
  background: #7169ff;
  width: 10px;
  height: 10px;
}

.navigation .nav-pannel .tabs li:before {
  bottom: -10px;
}

.navigation .nav-pannel .tabs li:after {
  top: -10px;
}

.navigation .nav-pannel .tabs li a:after,
.navigation .nav-pannel .tabs li a:before {
  width: 20px;
  height: 20px;
  /* Circles are circular */
  border-radius: 10px;
  background: #7169ff;
  /* Circles over squares */
  z-index: 2;
}

.navigation .nav-pannel .tabs .active a:after,
.navigation .nav-pannel .tabs .active a:before {
  background: #7169ff;
}

.navigation .nav-pannel .tabs li:first-child.active a:before,
.navigation .nav-pannel .tabs li:last-child.active a:after {
  background: #7169ff;
}

